@mixin mobile-360px {
  @media screen and (min-width: 360px) {
    @content;
  }
}
@mixin mobile-390px {
  @media screen and (min-width: 390px) {
    @content;
  }
}
@mixin mobile-min {
  @media screen and (min-width: 430px) {
    @content;
  }
}
@mixin mobile {
  @media screen and (min-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (min-width: 768px) {
    @content;
  }
}
@mixin desktop {
  @media screen and (min-width: 1100px) {
    @content;
  }
}
